@use "./typeface.scss";
@use "./colors.scss";
// ============= VARIABLES ================

// FONTS
@mixin font-display {
  font-family: "Uertas";
  font-size: max(10.3rem, 70px);
}

@mixin font-h2 {
  font-family: "BDO Grotesk";
  font-size: max(4.3rem, 40px);
  font-weight: 100;
  text-transform: uppercase;
}

@mixin font-h3 {
  font-family: "BDO Grotesk";
  font-size: max(3.5rem, 52px);
  font-weight: 900;
}

@mixin font-h4 {
  font-family: "BDO Grotesk";
  font-size: max(2.5rem, 20px);
  font-weight: 900;
  text-transform: uppercase;
}

@mixin font-decoration {
  font-family: "Coupeur Jack";
  font-size: max(1.9rem, 14px);
}

@mixin font-body {
  font-family: "Consolas";
  font-size: max(1.5rem, 16px);
}

@mixin font-body-alternative {
  font-family: "BDO Grotesk";
  font-size:  max(1.5rem, 24px);
}

// SIZES
$sizes: (
  "margin-1": 30px,
  "margin-2": 100px,
  "border": colors.$light
    solid 2px,
);
