@use "modules/util.scss" as *;
@use "modules/colors.scss";
@use "modules/global" as *;

.intro-text {
  border-left: 2px solid black;
}
.projects {
  @include flex(column);

  min-height: 80vh;
  height: fit-content;
  width: 100%;

  .left {
    @include section(light);
    @include flex(column);
    @include text();

    .top-title {
      @include flex(column);
      flex-grow: 1;

      h2 {
        @include border("dark", "-bottom");
      }
    }

    .links {
      flex-basis: 10%;
    }
  }

  .right {
    @include section(dark);
  }
}

.contact {
  @include flex(row);

  height: 100vh;
  justify-content: center;
  align-items: center;
  position: relative;

  .text {
    @include section("dark");
    @include text();

    text-align: center;
    width: fit-content;
  }
}

.front {
  @include flex(row);

  position: relative;
  flex-grow: 2;
  flex-basis: auto;
  height: 100vh;
  align-items: center;

  .text {
    * {
      width: fit-content;
    }

    h5 {
      position: relative;
      top: -1rem;
      @include border(colors.$light, "-top");
    }
  }
}

.who-am-i {
  @include flex(column-reverse);
  height: fit-content;
  min-height: 80vh;

  width: 100%;

  .left {
    @include section(dark);
    @include text();
    @include flex(column);
    gap: 20px;

    .card {
      @include border("");
      @include flex(column);
      gap: 10px;
      padding: 20px;

      .icons {
        height: 40px;
        @include flex(row);
        gap: 8px;

        img {
          height: 100%
        }
      }
    }
  }
  .right {
    @include section(light);
    @include flex(column);
    @include text();

    h2 {
      @include border(dark, "-bottom");
    }
  }
}



@media (min-width:320px)  { /* smartphones, iPhone, portrait 480x320 phones */ }
@media (min-width:481px)  { /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ }
@media (min-width:641px)  { /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */ }
@media (min-width:961px)  { /* tablet, landscape iPad, lo-res laptops ands desktops */ }
@media (min-width:1025px) { 
  .who-am-i {
    @include flex(row);

    .right {
      width: 30%;
    }

    .left{
      width: 70%;
    }
  }

  .projects {
    @include flex(row);

    .right {
      width: 70%;
    }

    .left {
      width: 30%;
    }
  }
 }
