@use "./modules/global" as *;
@use "modules/colors.scss";


.navbar {
  position: fixed;
  right: map-get($map: $sizes, $key: "margin-1");
  top: map-get($map: $sizes, $key: "margin-2");

  height: calc(100vh - 2 * map-get($sizes, "margin-2"));
  background-color: colors.$light;

  border-left: map-get($map: $sizes, $key: "border");

  visibility: hidden; // Hide on phones
}

@media (min-width:1281px) {
  .navbar {
    visibility: visible;
  }
}