@font-face {
  font-family: "Uertas Regular";
  src: url("/src/resources/fonts/UertasRegular.ttf");
}
@font-face {
  font-family: "BDO Grotesk";
  src: url("/src/resources/fonts/BDOGrotesk-VF.ttf");
}
@font-face {
  font-family: "Coupeur Jack Semibold";
  src: url("/src/resources/fonts/CoupeurJack-Semibold.otf");
}
.main {
  position: relative;
  text-align: center;
  background: rgb(0, 0, 0);
  background: radial-gradient(circle, rgba(0, 0, 0, 0.6) 70%, rgba(0, 0, 0, 0.8) 100%);
  background-attachment: fixed;
  padding: 30px;
}
.main .image {
  background-image: url("/src/resources/images/background.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}
.main .decorations .d2, .main .decorations .d3 {
  display: flex;
  flex-direction: row;
  position: absolute;
  right: 30px;
  align-items: center;
  flex-direction: row-reverse;
  gap: 30px;
}
.main .decorations .d2 .line1, .main .decorations .d3 .line1 {
  width: 200px;
  height: 2px;
  background-color: #fcf5da;
}
.main .decorations .d2 .line2, .main .decorations .d3 .line2 {
  flex-grow: 1;
  height: 2px;
  background-color: #fcf5da;
}
.main .decorations .d2 {
  top: 30px;
  width: calc(100vw - 460px);
}
.main .decorations .d3 {
  bottom: 30px;
  width: calc(100vw - 120px);
}
.main .decorations .d4 {
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 60px;
  left: 60px;
}

.header {
  color: #1b1b1b;
  background-color: #fcf5da;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  z-index: 2;
  filter: drop-shadow(0px 4px 4px black);
}
.header a img {
  height: 32px;
  width: 32px;
  margin: 10px 15px;
}

@media (min-width: 320px) { /* smartphones, iPhone, portrait 480x320 phones */ }
@media (min-width: 481px) { /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ }
@media (min-width: 641px) {
  .header {
    left: 20px;
    top: 20px;
    width: calc(100vw - 40px);
  }
  .main {
    text-align: left;
  }
}
@media (min-width: 961px) { /* tablet, landscape iPad, lo-res laptops ands desktops */ }
@media (min-width: 1025px) {
  .main .content {
    padding: 30px;
    padding-right: 60px;
  }
}

